import Vue from "vue";
import VueRouter from "vue-router";
import Layout from "@/layout/layout.vue";

Vue.use(VueRouter);

// push
const originalPush = VueRouter.prototype.push;
// push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch((err) => err);
};

/**
* path: '',
* redirect: noredirect,            noredirect  breadcrumb 
* name:'router-name',              <keep-alive> 
* componet: () => import(),       
* hidden: true,                    true  sidebar false
* isShowFirstLevel: true,                true , , 
* meta : {
    title: 'title'                sidebar  breadcrumb 
    icon: 'icon class'            sidebar 
  }
**/

export const routes = [
  {
    path: "/login",
    name: "Login",
    hidden: true,
    component: () =>
      import(/** webpackChunkName: 'group-login' */ "@/views/login/login.vue"),
  },
  {
    path: "/",
    redirect: "/dashboard",
    component: Layout,
    isShowFirstLevel: false,
    children: [
      {
        path: "dashboard",
        name: "Dashboard",
        component: () =>
          import(
            /** webpackChunkName: 'group-dashboard' */ "@/views/dashboard/dashboard.vue"
          ),
        meta: { title: "", icon: "home" },
      },
    ],
  },
  {
    path: "/component-package",
    redirect: "/component-package/form-package",
    name: "ComponentPackage",
    component: Layout,
    isShowFirstLevel: true,
    meta: { title: "", icon: "deployment-unit" },
    children: [
      {
        path: "form-package",
        name: "ComponentPackageFormPackage",
        component: () =>
          import(
            /** webpackChunkName: 'group-component-package' */ "@/views/component-package/form-package/form-package.vue"
          ),
        meta: { title: "", icon: "" },
      },
    ],
  },
  {
    path: "/test",
    redirect: "/test/test-first/test-second",
    name: "Test",
    component: Layout,
    isShowFirstLevel: true,
    meta: { title: "", icon: "cluster" },
    children: [
      {
        path: "test-first",
        redirect: "/test/test-first/test-second",
        name: "TestFirst",
        component: () =>
          import(
            /** webpackChunkName: 'group-test' */ "@/views/test-father/father.vue"
          ),
        meta: { title: "", icon: "" },
        children: [
          {
            path: "test-second",
            name: "TestFirstTestSecond",
            component: () =>
              import(
                /** webpackChunkName: 'group-test' */ "@/views/test-father/test-son/son.vue"
              ),
            meta: { title: "", icon: "" },
          },
        ],
      },
    ],
  },
];

export default new VueRouter({
  base: process.env.BASE_URL,
  routes: routes,
});
